import api from '@/common/vmeitime-http/'
export default {
	data() {
		return {
			transform: 'translateY(50vh)',
			timer: 0,
			backgroundColor: 'rgba(0,0,0,0)',
			show: false,
			config: {},
		};
	},
	props:{
		contentHeight:{
			type: Number,
			default: 0
		},
		//是否是tabbar页面
		hasTabbar:{
			type: Boolean,
			default: false
		},
		shareList:{
			type: Array,
			default: function(){
				return [];
			}
		},
		sharedata:{
			type: Object,
			default: function(){
				return {};
			}
		}
	},
	created() {
		const height = uni.upx2px(this.contentHeight) + 'px';
		this.config = {
			height: height,
			transform: `translateY(${height})`,
			backgroundColor: 'rgba(0,0,0,.4)',
		}
		this.transform = this.config.transform;
	},
	methods:{
		onShareAppMessage(res) {
			return {
			  title: this.sharedata.name,
			  path: '/pages/procuct/product?id='+this.sharedata.id
			}
		},
		toggleMask(){
			//防止高频点击
			if(this.timer == 1){ return; }
			this.timer = 1;
			setTimeout(()=>{ this.timer = 0; }, 500)
			if(this.show){
				this.transform = this.config.transform;
				this.backgroundColor = 'rgba(0,0,0,0)';
				setTimeout(()=>{
					this.show = false;
					this.hasTabbar && uni.showTabBar();
				}, 200)
				return;
			}
			this.show = true;
			//等待mask重绘完成执行
			if(this.hasTabbar){
				uni.hideTabBar({
					success: () => {
						setTimeout(()=>{
							this.backgroundColor = this.config.backgroundColor;
							this.transform = 'translateY(0px)';
						}, 10)
					}
				});
			}else{
				setTimeout(()=>{
					this.backgroundColor = this.config.backgroundColor;
					this.transform = 'translateY(0px)';
				}, 10)
			}
		},
		makehaibao() {
			this.toggleMask();
			// let access_token = uni.getStorageSync('access_token');
			// if(access_token) this.getqrcode(access_token)
			// else this.acesstokenurls()
		},
		getqrcode (access_token) {
			let tokenurl = 'https://api.weixin.qq.com/wxa/getwxacodeunlimit?access_token='+access_token;
			api.httpAcesstoken(tokenurl).then((res) => {
				console.log(res.data)
			})
		},
		acesstokenurls () {
			let acesstokenurl = 'https://api.weixin.qq.com/cgi-bin/token?grant_type=client_credential&appid=wx51775e4f2a1f1c78&secret=9b430e6a08f798c03cde84758448d35f';
			api.httpAcesstoken(acesstokenurl).then((res) => {
				if(res.data.access_token) {
					uni.setStorageSync('access_token', res.data.access_token)
					this.getqrcode()
				}
			})
		},
		//防止冒泡和滚动穿透
		stopPrevent(){}
	}
}
